

import report from '@/utils/statistics'
import '@/common/css/common.css'
import '@/common/css/self.css'
import sfApi from '@/store/apis/sfFestival'

import { mapState, mapActions, mapMutations } from 'vuex'
import { session, local } from '@/utils/storage'
import Wechat from '@/utils/wechat'
import uuidv4 from 'uuid/v4'
import shareWin from '@/components/shareWin'

import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'




export default {
directives: {
TransferDom
},
components: {
Divider,
XButton,
Confirm,
XDialog,
Loading,
Toast,
shareWin
},
data() {
return {
showWin: false,
couponList: [],
getPrised: false, //是否开过奖了

showShareMask: false, //显示分享时候的蒙层
from_uuid: null, //外面链接传过来的uuid
card_uuid: null, //自动生成用于分享
cardInfo: {}, //底部卡片的信息
type: null, //从外面点击进入的链接，只有get 或send 两种情况
outCard: {}, //外部卡片的信息
inOuterCard: false, //是否是在外来卡的界面

outCardAccept: false, //是同意接受外来链接的请求
outCardAcceptSuc: true, //接受或者赠送是否成功

index: 0, //显示第几张自己的卡片
randomBrand: {}, //随机头部信息
cardHeader: {}, //头部显示的模版
cardArr: [],
inRandomCard: false, //是否正在展示随机获得的卡片
firstCardGeted: false, //第一次的卡片是否获取了
isFirst: false, //是否是第一次进入
firstCard: {} 
}
},
beforeRouteLeave(to, from, next) {
next()
},
beforeDestroy() {},
methods: {
...mapActions({

}),
...mapMutations({

}),
closeWin() {
this.showWin = false
},
kaijiang() {
this.showWin = true
report('春节活动落地页', '点击', '开奖按钮')
},
doKJAction() {
sfApi.kaijiang().then(data => {
if(!data.data) return;
this.couponList = data.data
this.getPrised = true
}).catch(this.$Err)
},
seeQuan() {
this.$router.push('/coupons')
},

getWxConfig(cb) {
if(this.wxShareCB == null) this.wxShareCB = cb;
this.refreshShare()

},
showShareMaskFun() {
this.showShareMask = true
report('我的卡片页面', '点击', this.isGive ? '送一张给朋友' : '叫朋友送一张')
},

refreshShare() {

var type = this.isGive ? 'send' : 'get';
var uuid = this.card_uuid;
if(this.$isStrEmpty(uuid)) {
this.$toast('uuid is empty')
return;
}

var cardName = this.getCardName(this.index)

var obj = {
link: location.origin + location.pathname + '?type=' + type + '&from_uuid=' + uuid,
title: '分享标题',
desc: '分享的内容'
}

if(this.inRandomCard || this.inOuterCard) { //随机卡片就分享链接
obj.link = location.href
obj.title = '和+邀您新春集字卡！集齐中奖100%！',
obj.desc = '百万大奖等你拿！'
} else {
if(type == 'send') {
obj.title = '和+邀您新春集字卡！集齐中奖100%'
obj.desc = '送你一张' + cardName + '字卡'
} else {
obj.title = '还差一张字卡就集齐了，快来帮帮ta！'
obj.desc = '求一张' + cardName + '字卡'
}
}

Wechat.initShare(obj, this.wxShareCB)
},

loadCardData(cb) {
sfApi.getFuCards().then(data => {
if(!data.data) return;
this.cardInfo = data.data
this.cardArr[0] = this.cardInfo.heplus_count
this.cardArr[1] = this.cardInfo.p_count
this.cardArr[2] = this.cardInfo.l_count
this.cardArr[3] = this.cardInfo.u_count
this.cardArr[4] = this.cardInfo.s_count
this.cardArr = [...this.cardArr]

setTimeout(() => { //分享更新后重新加载分享数据
cb && cb()
}, 200)

}).catch(this.$Err)
},
changeIndex(index) {
this.index = index;
this.showSelfCard()

var cardName = this.getCardName(this.index)

cardName += '字卡'

report('我的卡片页面', '点击', cardName)

},
getCardName(index, cvt) {
index *= 1
var cardName = '未知'
if(cvt) { //转化
index += 1
if(index == 5) index = 0;
}

if(index == 0) cardName = '"和+"';
if(index == 1) cardName = '"P"';
if(index == 2) cardName = '"L"';
if(index == 3) cardName = '"U"';
if(index == 4) cardName = '"S"';

return cardName;
},
acceptOutCard() { //接受外面赠送或者索取的链接操作
if(this.$isStrEmpty(this.from_uuid)) {
this.$toast('from_uuid is empty!')
return;
}

sfApi.agreeSend(this.from_uuid).then(data => {
this.outCardAccept = true
if(data.data) {
if(data.data.type == 1) {
this.outCardAcceptSuc = true 
} else this.outCardAcceptSuc = false //领取慢了已经被别人领取了。
this.loadCardData();
}

}).catch(this.$Err)
},
setRandomBrand() {
sfApi.getRandomBrand().then(data => {
this.randomBrand = data.data
this.cardHeader = {
name: this.randomBrand.name,
image: this.randomBrand.image,
lucky_text: this.randomBrand.lucky_text
}
}).catch(this.$Err)
},

sendCard(cb) {
var request_type = this.isGive ? 1 : 0;
var card_type = this.index - 1;

if(card_type == -1) card_type = 4;
var uuid = this.card_uuid;

if(this.$isStrEmpty(request_type)) {
this.$toast('request_type is empty')
return;
}

if(this.$isStrEmpty(card_type)) {
this.$toast('card_type is empty')
return;
}

if(this.$isStrEmpty(uuid)) {
this.$toast('uuid is empty')
return;
}

sfApi.sendCard({
request_type,
card_type,
uuid,
}).then(data => {
cb && cb(data)
}).catch(this.$Err)

report('我的卡片页面', '次数', this.isGive ? '送朋友字卡页面分享' : '叫朋友送字卡页面分享')
},
attendAct() {
this.$router.push('/sfhome')
report('我的卡片页面', '点击', '参与活动赢字卡按钮')
},

isFirstIn() {
return new Promise(function(res, rej) {
sfApi.sfInfo().then(data => {
res(data.data.is_first)
}).catch(err => rej(err))
})
},
setRandomCard() { //获取系统随机卡片
sfApi.getRandomCard().then(data => {
if(!data.data) return;
this.firstCard = data.data
}).catch(this.$Err)
},
showFirstCard() { //显示第一次自动获取的卡
this.inRandomCard = true
this.setRandomBrand()
this.setRandomCard()
this.refreshShare();
},
getFirstCard() { //接受系统自动的卡片
if(this.firstCardGeted) return;
sfApi.acceptFirstCard().then(data => {
this.firstCardGeted = true
this.loadCardData();
report('我的卡片页面', '点击', '立刻领取')
}).catch(this.$Err);
},
showSelfCard() { //第一次进入显示自己的卡片或者点击下方卡片后的选择
this.setRandomBrand()
this.inRandomCard = false
this.inOuterCard = false
this.refreshShare()
},
showOtherCard(type, uuid) { //显示别人链接的卡
sfApi.getSendCardDetail(uuid).then(data => {

var obj = data.data
if(!obj) return;

this.outCard = obj;

var cardName = this.getCardName(obj.card_type, true)
var text = '送你一张'
if(this.type == 'get') text = '求一张'
text += cardName + '卡'
this.cardHeader = {
lucky_text: text,
image: obj.avatar_url,
name: obj.nickname
}



}).catch(this.$Err)





this.refreshShare();
},
showCard(index, type, uuid) { //显示随机，自己的卡片，或者别人的链接卡片
if(type) { //点击别人的链接进入
if(this.$isStrEmpty(uuid)) {
this.$toast('uuid is empty');
return;
}
this.showOtherCard(type, uuid)
} else { //自己点击卡片进来
this.isFirstIn().then(first => {
this.isFirst = !!first;
if(this.isFirst) {
this.showFirstCard()
} else this.showSelfCard();
}).catch(e => {
this.$Err(e)
this.showSelfCard();
})

}
},
itemObj(i) {
var obj = {}
obj.name = '和+卡';
obj.img = 'IH.png'
if(i == 0) {
obj.name = '和+卡';
obj.img = 'IH.png'
} else if(i == 1) {
obj.name = 'P卡';
obj.img = 'IP.png'
} else if(i == 2) {
obj.name = 'L卡';
obj.img = 'IL.png'
} else if(i == 3) {
obj.name = 'U卡';
obj.img = 'IU.png'
} else if(i == 4) {
obj.name = 'S卡';
obj.img = 'IS.png'
}

if(this.cardArr[i] > 0) obj.img = obj.img.substr(1);

return obj;
}

},
created() {
this.card_uuid = uuidv4() 
this.getWxConfig(() => { //分享成功后
this.sendCard(() => { //数据库记录成功
this.card_uuid = uuidv4() 
this.loadCardData(() => {
this.refreshShare()
}) //刷新下面的数值
})

})
this.loadCardData()
var index = this.$route.query.index
if(index == null) index = 0;
this.index = index;
var type = this.$route.query.type 
this.type = type
if(!this.$isStrEmpty(type)) { //是外来卡
this.inOuterCard = true
}

var from_uuid = this.$route.query.from_uuid 
this.from_uuid = from_uuid

this.showCard(this.index, this.type, from_uuid)
},

mounted() {

},
computed: {
...mapState({
myInfo: state => state.user.myInfo,
mySource: state => state.user.mySource
}),

calOutBTTxt() {
var name = '未知'

if(this.outCardAccept) {
if(this.type == 'send') name = '领取成功';
else name = '赠送成功';
} else {
if(this.type == 'send') name = '立即领取';
else name = '送给TA';
}

return name;
},
cardNum() {

var index = this.selCardIndex

var num = this.cardArr[index];

if(num == null) num = 0;
if(num < 0) num = 0;

return num;
},

isFinished() { //全部卡是否集齐
return this.cardArr.length == 5 && this.cardArr.every((item) => {
return item > 0;
});
},

currentObj() {
var obj = {}
var name = ''

var selCardIndex = this.selCardIndex

if(selCardIndex == 0) name = 'HB.png';
else if(selCardIndex == 1) name = 'PB.png';
else if(selCardIndex == 2) name = 'LB.png';
else if(selCardIndex == 3) name = 'UB.png';
else if(selCardIndex == 4) name = 'SB.png';

if(this.cardNum == 0 && !this.isForceHigh) {
name = 'I' + name;
}

obj.name = name
return obj;
},

isForceHigh() { //强制高亮
var force = false;
if(this.inRandomCard && this.firstCard.card_type != null) { //系统随机的情况
force = true
}

if(this.inOuterCard && this.outCard.card_type != null) { //外来卡的情况
force = true
}

return force;
},

isGive() { //是否是赠送
return this.cardNum > 0;
},
selCardIndex() {
var index = this.index
if(this.inRandomCard && this.firstCard.card_type != null) { //系统随机的情况

index = this.firstCard.card_type * 1 + 1
if(isNaN(index)) {
console.error('index is NaN')
index = 0
}
if(index == 5) index = 0;

}

if(this.inOuterCard && this.outCard.card_type != null) { //外来卡的情况

index = this.outCard.card_type * 1 + 1
if(isNaN(index)) {
console.error('index is NaN')
index = 0
}
if(index == 5) index = 0;
}

return index;
},
selCard() {
var name = ''
var index = this.selCardIndex
if(index == 0) name = 'H.png';
if(index == 1) name = 'P.png';
if(index == 2) name = 'L.png';
if(index == 3) name = 'U.png';
if(index == 4) name = 'S.png';

return name;
}
},

}
